.topGrid {
    background-image: url('./blue_bg.svg');
    background-color: #f1f1f1;
    background-repeat: no-repeat;
    background-size: contain;
    background-size: 2500px ;
}


 .transparentHeader {
     background-color: transparent;
     color: white;
 }

 .mainLogoCenter {
    display: block;
    text-align: center;
    /* padding: 30px; */
 }

 .mainLogo {
     width: 220px;
     height: 220px;
     background-color: white;
     padding: 4px;
     border-radius: 50%;
 }

 .echoClass {
    
    color: white;
    font-size: 55px;
    margin-top: -8px;
}

.mottoID {
    
    color: white;
    font-size: 15px;
    margin-top: -8px;
}

.computerParent {
    margin-top: 20px;
    margin-bottom: 20px;
    display: block;
    text-align: center;
}

.computer {
    
    width: 700px;
    margin-right: 40px;
 
}

.iconsContainer {
    margin: 0 40px 0 40px;
}

.iconsImage {
    width: 100%;
    padding: 30px 0 30px 0;
}

.product {
    padding-top: 20px;
    padding-bottom: 20px;
}

.anonParent {
    background-image: url('./svgs for content/anon_bg.svg');
    background-repeat: no-repeat;
    background-size: fill;
    background-position-y:350px; 
}

.anonButtonCenterClass {
    width: 100%;
    text-align: center;
}

.mappParent {
    background-image: url('./svgs for content/mapp_bg.svg');
    background-repeat: no-repeat;
    background-size: fill;
    background-position-y: 180px;
}

.juliParent {
    background-image: url('./svgs for content/juli_bg.svg');
    background-repeat: no-repeat;
    background-size: fill;
    background-position-y:150px; 
}

.gridImage {
    width: 100%;
    
}

.avatarClass {
    text-align: center;
}

.imgTag {
    width: 70px;
    height: 70px;
    border-radius: 50%;
    margin-top: 70px;
}

.invisionTeamName {
    width: 100%;
    text-align: center;
    padding-bottom: 20px;
}

.invisionMarketingTeam {
    width: 100%;
    text-align: center;
}

.sendButton {
    padding-top: 30px;
    width: 100%;
    text-align: center;
}

.invisionLogoFooterParent {
    display: block;
    text-align: center;
    padding: 30px;
}

.invisionLogoFooter {
    width: 100px;
}


.cottonAppImage {
    width: 100%;

}

.cottonAppHeader {
    font-size: 40px;
    color: #757575;
}


.footerText {
    padding-top: 20px;
}

.copyright {
    margin-bottom: 10px;
}

#cardID {
    margin-bottom: 80px;
}





@media (max-width: 1100px) {

    .computerParent {
        margin-top: 20px;
        margin-bottom: 20px;
        display: block;
        text-align: center;
        margin-right: 40px;
    }  
    .computer {      
        width: 100%;            
    }
}

@media (max-width: 950px) {
    .cottonAppHeader {
        font-size: 30px;
        color: #757575;
    }
    
}


@media (max-width: 800px) {

    .echoClass {
        font-size: 35px;
        margin-bottom: 70px;
    }

    .mottoID {
        font-size: 20px;
        text-align: center;
    }

    .mainLogo {
        width: 160px;
        height: 160px;
    }


    .computer {
        padding-left: 0px;
        padding-bottom: 4px;
    }
}

@media (max-width: 600px) {

    .footerText {
        width: 100%;
        text-align: center;
        padding-top: 0;
    }

    .echoClass {
    
        color: white;
        width: 100%;
        text-align: center;
        font-size: 25px;
        margin-top: -8px;
        padding-bottom: 30px;
    }

    .mappParent {
        background-image: url('./svgs for content/mapp_bg.svg');
        background-repeat: no-repeat;
        background-size: fill;
        background-position-y:180px; 
    }

    .mottoID {
        font-size: 15px;
        width: 100%;
        text-align: center;
    }

    .mainLogoCenter {
        padding: 0;
     }

    .mainLogo {
        width: 140px;
        height: 140px;
        padding: 5px;
    }


    .iconsContainer {
        margin: 0 10px 0 10px;
    }

    .writeToUs {
        width: 100%;
        text-align: center;
        margin-bottom: 10px
        
    }


    .computerParent {
        margin-top: 20px;
        margin-bottom: 20px;
        display: block;
        text-align: center;
        margin-right: 20px;
    }
    
    .computer {
        
        width: 100%;
        
     
    }

    .anonParent {
        background-image: url('./svgs for content/anon_bg.svg');
        background-repeat: no-repeat;
        background-size: fill;
        background-position-y:380px; 
    }

    .juliParent {
        background-image: url('./svgs for content/juli_bg.svg');
        background-repeat: no-repeat;
        background-size: fill;
        background-position-y:180px; 
    }

    .cottonAppHeader {
        font-size: 25px;
        color: #757575;
    }

}

@media (min-width: 2500px) {
    .topGrid {
        background-image: url('./blue_bg.svg');
        background-color: #f1f1f1;
        background-repeat: no-repeat;
        background-size: contain;
        background-size: 100% ;
    }
}




